import { mapState } from "vuex";
import appIndex from '../components/app-index/app-index';
import appCondition from '../components/app-condition/app-condition';
import appRichText from "../../../components/basic-component/app-rich/parse.vue";

export default {
    data() {
        return {
            detail: {
                bonus_rate: 0
            },
            protocol: null,
            setting: {},
            name: '',
            mobile: '',
            wait: false,
            refuse: false,
            remove: false,
            read: false,
            look: false,
            beApply: false,
            rate: 0,
            template_message: [],
            height: 0,
            status: false,
            check: {}
        }
    },
    components: {
        'app-index': appIndex,
        "app-rich-text": appRichText,
        'app-condition': appCondition,
    },
    computed: {
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            bonusImg: state => state.mallConfig.__wxapp_img.bonus,
        })
    },
    methods: {
        toRead() {
            this.protocol = this.setting.agreement_content;
        },
        reApply() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.wait = false;
            that.$request({
                url: that.$api.stock.clear,
                method: 'post'
            }).then(response=>{
                if(response.code == 0) {
                    that.getDetail();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        receive(e) {
            this.beApply = true;
        },
        beRead() {
            this.read = !this.read;
        },
        toIndex() {
            uni.navigateTo({
                url: '/pages/index/index'
            }); 
        },
        imageLoad: function (e) {
            var $width = e.detail.width; //获取图片真实宽度
            var $height = e.detail.height; //获取图片真实高度
            this.height = $height * (750 / $width) / 2
        },
        submit() {
            let that = this;
            let is_agree = 0;
            if(that.read || that.setting.is_agreement == 0) {
                is_agree = 1;   
            }else {
                uni.showToast({
                    title: that.setting.agreement_title ? '请先查看' + that.setting.agreement_title +'申请协议并同意' : '请先查看股东分红申请协议并同意',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            }
            if(that.setting.apply_type < 3) {
                if( that.name && that.mobile) {
                    if(that.mobile.length == 11 && (/0?(1)[0-9]{10}/.test(that.mobile))) {
                        this.$subscribe(this.template_message).then(res => {
                            this.submitApply(is_agree);
                        }).catch(res => {
                            this.submitApply(is_agree);
                        });
                    }else {
                        uni.showToast({
                            title: '请输入正确的手机号码',
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }else {
                    uni.showToast({
                        title: '请完善申请信息',
                        icon: 'none',
                        duration: 1000
                    });
                }
            }else {
                this.$subscribe(this.template_message).then(res => {
                    this.submitApply(is_agree);
                }).catch(res => {
                    this.submitApply(is_agree);
                });
            }
        },
        submitApply(is_agree) {
            let that = this;
            that.$request({
                url: that.$api.stock.apply,
                data: {
                    name: that.name,
                    mobile: that.mobile,
                    is_agree: is_agree
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    that.name = '';
                    that.mobile = '';
                    setTimeout(function() {
                        uni.showLoading({
                            mask: true,
                            title: '加载中...'
                        });
                        that.getStatus();
                        that.look = false;
                        if(that.setting.apply_type == 1 || that.setting.apply_type == 3) {
                            that.wait = true;
                        }
                    }, 500)
                }else {
                    if (response.msg == '请先查看股东分红申请协议并同意') {
                        response.msg = '请先查看' + that.setting.agreement_title +'申请协议并同意'
                    }
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            }); 
        },
        getStatus() {
            let that = this;
            that.wait = false;
            that.status = false;
            that.$request({
                url: that.$api.stock.status,
            }).then(response=>{
                that.status = true;
                if(response.code == 0) {
                    that.detail = response.data.stock;
                    if(response.data.stock.name) {
                        that.name = response.data.stock.name;
                    }
                    if(response.data.stock.phone) {
                        that.mobile = response.data.stock.phone;
                    }
                    if(that.detail.status == -1) {
                        that.$hideLoading();
                        uni.hideLoading();
                        that.beApply = false;
                        that.remove = true;
                    }else if(that.detail.status == 2) {
                        that.$hideLoading();
                        uni.hideLoading();
                        that.beApply = false;
                        that.refuse = true;
                    }else if(that.detail.status == 1) {
                        that.$hideLoading();
                        uni.hideLoading();
                        that.beApply = false;
                        return false;
                    }else if(that.detail.status == 0) {
                        that.$hideLoading();
                        uni.hideLoading();
                        that.beApply = false;
                        that.wait = true;
                    }else {
                        that.getDetail();
                    }
                }else {
                    if(response.msg == '用户未申请股东') {
                        that.getDetail();
                    }else {
                        that.$hideLoading();
                        uni.hideLoading();
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.stock.setting,
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.setting = response.data;
                    that.template_message = response.data.template_message_stock;
                    uni.setNavigationBarTitle({
                        title: that.setting.form.title?that.setting.form.title: '股东分红',
                    })
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        getDetail() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.check = {};
            that.$request({
                url: that.$api.stock.index,
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.look = true;
                    that.refuse = false;
                    that.remove = false;
                    that.check = response.data;
                    if(that.check.all_money) {
                        that.rate = +that.check.all_money / +that.check.condition *100;
                    }else if(that.check.all_order) {
                        that.rate = +that.check.all_order / +that.check.condition *100;
                    }else if(that.check.all_money) {
                        that.rate = +that.check.all_money / +that.check.condition *100;
                    }else if(that.check.cash_money) {
                        that.rate = +that.check.cash_money / +that.check.condition *100;
                    }else if(that.check.total_money) {
                        that.rate = +that.check.total_money / +that.check.condition *100;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                    setTimeout(function(){
                        that.toIndex();
                    },1000)
                }
            }).catch(response => {
                uni.hideLoading();
            });
        }
    },
    onShow() {
        let that = this;
        that.getStatus();
    },

    onLoad(options) { this.$commonLoad.onload(options);
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        if(options.name) {
            this.name = options.name
        }
        if(options.mobile) {
            this.mobile = options.mobile
        }
        this.getSetting();
    }
}
